<template>
    <div class="course">
        <Breadcrumb name1="" name2=""/>
        <!--  -->
        <el-card class="box-card">
            <el-row :gutter="20">
                <el-col :span="16">
                    <el-form :inline="true" :model="formCourse">
                        <el-form-item label=":">
                            <el-input v-model="formCourse.coursename" placeholder="" style="width: 160px"></el-input>
                        </el-form-item>
                        <el-form-item label=":" prop="classname">
                            <el-select v-model="formCourse.classname" placeholder="" style="width: 160px">
                                <el-option
                                v-for="item in ClassName"
                                :key="item.id"
                                :label="item.name"
                                :value="item.name">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="getCourseList"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <el-col :span="4">
                    <!--  -->
                    <el-button type="primary" @click="addDialogVisible = true"></el-button>
                </el-col>
            </el-row>
            <el-table :data="courseData.courseList" stripe style="widt: 100%" border>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="coursename" label=""></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="courseweek" label=""></el-table-column>
                <el-table-column prop="coursetype" label=""></el-table-column>
                <el-table-column prop="classname" label=""></el-table-column>
                <el-table-column prop="credit" label=""></el-table-column>
                <el-table-column label="" width="180px">
                <template v-slot="scope">
                  <el-button type="primary" icon="el-icon-circle-plus-outline" size="mini" @click="showAddTableDialog(scope.row.courseid)"></el-button>
                  <!--  -->
                  <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.courseid)"></el-button>
                  <!--  -->
                  <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeCourseById(scope.row.courseid)"></el-button>
                </template>
                </el-table-column>
            </el-table>
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                @size-change="handleSizeChange"
                :page-size="pageSize"
                :page-sizes="[5, 9, 13, 15]"
                layout="total, sizes, prev, pager, next, jumper"
                :total="courseData.total"
            >
            </el-pagination>
        </el-card>
        <el-dialog title="" @close="addTableClosed" :visible.sync="addTableDialogVisble" width="35%">
            <el-form :model="addTableForm" :rules="addFormRules" ref="addTableFormRef" label-width="90px">
                <el-form-item label="" prop="coursename" style="width: 60%">
                    <el-input v-model="addTableForm.coursename" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name" style="width: 60%">
                    <el-input v-model="addTableForm.name" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="coursetime" style="width: 60%">
                    <el-input v-model="addTableForm.coursetime"></el-input>
                </el-form-item>
                <el-form-item label="" prop="classroom" style="width: 60%">
                    <el-input v-model="addTableForm.classroom"></el-input>
                </el-form-item>
                <el-form-item label="" prop="courseweek" style="width: 40%">
                    <el-input v-model="addTableForm.courseweek" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="coursetype" style="width: 40%">
                    <el-input v-model="addTableForm.coursetype" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="classname" style="width: 60%">
                    <el-input v-model="addTableForm.classname" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="credit" style="width: 40%">
                    <el-input v-model="addTableForm.credit" :disabled="true"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addTableDialogVisble = false"> </el-button>
                <el-button type="primary" @click="addCourseTable"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="addDialogVisible" width="35%" @close="addDislogClosed">
        <!--  -->
        <el-form label-width="90px" ref="addFormRef" :model="addForm" :rules="addFormRules">
            <el-form-item label="" prop="coursename" style="width: 60%">
                <el-input v-model="addForm.coursename"></el-input>
            </el-form-item>
            <el-form-item label="" prop="courseweek" style="width: 40%">
                <el-input v-model="addForm.courseweek"></el-input>
            </el-form-item>
            <el-form-item label="" prop="coursetype">
                <el-select v-model="addForm.coursetype" placeholder="" style="width: 160px">
                    <el-option
                    v-for="item in CourseType"
                    :key="item.value"
                    :label="item.label"
                    :value="item.label">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="classname">
                <el-select v-model="addForm.classname" placeholder="" style="width: 160px">
                    <el-option
                    v-for="item in ClassName"
                    :key="item.id"
                    :label="item.name"
                    :value="item.name">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="credit" style="width: 40%">
                <el-input v-model="addForm.credit"></el-input>
            </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addCourse"> </el-button>
        </span>
        </el-dialog>
        <el-dialog title="" @close="aditClosed" :visible.sync="editDialogVisble" width="35%">
            <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="90px">
                <el-form-item label="" prop="coursename" style="width: 60%">
                    <el-input v-model="editForm.coursename"></el-input>
                </el-form-item>
                <el-form-item label="" prop="courseweek" style="width: 40%">
                    <el-input v-model="editForm.courseweek"></el-input>
                </el-form-item>
                <el-form-item label="" prop="coursetype">
                    <el-select v-model="editForm.coursetype" placeholder="" style="width: 160px">
                        <el-option
                        v-for="item in CourseType"
                        :key="item.value"
                        :label="item.label"
                        :value="item.label">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="classname">
                    <el-select v-model="editForm.classname" placeholder="" style="width: 160px">
                        <el-option
                        v-for="item in ClassName"
                        :key="item.id"
                        :label="item.name"
                        :value="item.name">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="credit" style="width: 40%">
                    <el-input v-model="editForm.credit"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisble = false"> </el-button>
                <el-button type="primary" @click="editCourseInfo"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { courseAddFormRulesMixin } from '@/common/mixin.js'
import Breadcrumb from '../content/breadcrumb/Breadcrumb.vue'
export default {
  mixins: [courseAddFormRulesMixin],
  data () {
    return {
      // 
      currentPage: 1,
      // 
      pageSize: 5,
      // 
      formCourse: { coursename: '', classname: '' },
      ClassName: [{ name: '', id: '' }],
      CourseType: [{ label: '', value: '' }, { label: '', value: '' }],
      courseData: {
        courseList: [],
        total: 0
      },
      addTableDialogVisble: false,
      addTableForm: {},
      addDialogVisible: false,
      addForm: {},
      editDialogVisble: false,
      userInfo: {},
      editForm: {}
    }
  },
  methods: {
    async getClassName () {
      const { data: res } = await this.$http.get('util/class')
      this.ClassName = res.data
      // console.log(this.ClassName)
      // console.log(this.DepartmentName)
    },
    async getCourseList () {
      const { data: res } = await this.$http.get('teacher/course/' + this.currentPage + '/' + this.pageSize, {
        params: this.formCourse
      })
      if (res.code === 20041) {
        // this.$message.success(res.msg)
        this.courseData.courseList = res.data.records
        this.courseData.total = res.data.total
      } else {
        this.$message.error(res.msg)
      }
      this.formCourse = { coursename: '', classname: '' }
      // console.log(res)
    },
    addCourse () {
      this.$refs.addFormRef.validate(async valid => {
        console.log(valid)
        if (!valid) return
        // 
        const { data: res } = await this.$http.post('teacher/course', this.addForm)
        if (res.code !== 20011) {
          return this.$message.error('')
        }
        // 
        this.addDialogVisible = false
        // ,
        this.getCourseList()
        return this.$message.success('')
      })
    },
    //  pagesize  
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.pageSize = newSize
      this.getCourseList()
    },
    //    
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.currentPage = newPage
      this.getCourseList()
    },
    // 
    addDislogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    addTableClosed(){
      this.$refs.addTableFormRef.resetFields()
    },
    addCourseTable () {
      this.$refs.addTableFormRef.validate(async valid => {
        console.log(valid)
        if (!valid) return
        // 
        const { data: res } = await this.$http.post('teacher/courseTable', this.addTableForm)
        if (res.code !== 20011) {
          return this.$message.error('')
        }else{
          this.$message.success('')
        }
        this.addTableDialogVisble = false
      })
    },
    async showAddTableDialog (courseid) {
      const { data: res } = await this.$http.get('teacher/course/' + courseid)
      if (res.code !== 20041) {
        return this.$message.error('~')
      }
      this.addTableForm = res.data
      this.addTableDialogVisble = true
      // return this.$message.success('~')
    },
    async showEditDialog (courseid) {
      const { data: res } = await this.$http.get('teacher/course/' + courseid)
      if (res.code !== 20041) {
        return this.$message.error('~')
      }
      this.editForm = res.data
      this.editDialogVisble = true
      // return this.$message.success('~')
    },
    // 
    aditClosed () {
      this.$refs.editFormRef.resetFields()
    },
    editCourseInfo () {
      this.$refs.editFormRef.validate(async valid => {
        console.log(valid)
        if (!valid) return
        // 
        const { data: res } = await this.$http.put('teacher/course', this.editForm)
        if (res.code !== 20031) {
          return this.$message.error('!')
        }else{
          this.$message.success('!')
        }
        this.editDialogVisble = false
        this.getCourseList()
      })
    },
    // id
    async removeCourseById (courseid) {
      // 
      const confirmRusult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      console.log(confirmRusult)
      // , confirm
      // , cancel
      if (confirmRusult !== 'confirm') {
        return this.$message.info('')
      }
      this.$http.delete('teacher/course/' + courseid).then(res => {
        const { data: response } = res
        console.log(response)
        if (response.code !== 20021) {
          return this.$message.error('')
        }else{
          this.$message.success('')
        }
        this.getCourseList()
      })
    }
  },
  created () {
    this.getClassName()
    this.getCourseList()
  },
  components: {
    Breadcrumb
  }
}
</script>

<style lang="less" scoped>
</style>
