<template>
    <div class="courseTable">
        <Breadcrumb name1="" name2=""/>
        <!--  -->
        <el-card class="box-card">
            <el-row :gutter="20">
                <el-col :span="16">
                    <el-form :inline="true" :model="formCourse">
                        <el-form-item label=":">
                            <el-input v-model="formCourse.coursename" placeholder="" style="width: 160px"></el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="formCourse.name" placeholder="" style="width: 160px"></el-input>
                        </el-form-item>
                        <el-form-item label=":" prop="classname">
                            <el-select v-model="formCourse.classname" placeholder="" style="width: 160px">
                                <el-option
                                v-for="item in ClassName"
                                :key="item.id"
                                :label="item.name"
                                :value="item.name">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="getCourseList"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
            <el-table :data="courseData.courseList" stripe style="widt: 100%" border>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="coursename" label=""></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="coursetime" label=""></el-table-column>
                <el-table-column prop="classroom" label=""></el-table-column>
                <el-table-column prop="courseweek" label=""></el-table-column>
                <el-table-column prop="coursetype" label=""></el-table-column>
                <el-table-column prop="classname" label=""></el-table-column>
                <el-table-column prop="credit" label=""></el-table-column>
                <el-table-column label="" width="180px">
                <template v-slot="scope">
                    <!--  -->
                    <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                    <!--  -->
                    <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeCourseById(scope.row.id)"></el-button>
                </template>
                </el-table-column>
            </el-table>
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                @size-change="handleSizeChange"
                :page-size="pageSize"
                :page-sizes="[5, 9, 13, 15]"
                layout="total, sizes, prev, pager, next, jumper"
                :total="courseData.total"
            >
            </el-pagination>
        </el-card>
        <el-dialog title="" @close="aditClosed" :visible.sync="editDialogVisble" width="35%">
            <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="90px">
                <el-form-item label="" prop="coursename" style="width: 60%">
                    <el-input v-model="editForm.coursename" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name" style="width: 60%">
                    <el-input v-model="editForm.name" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="coursetime" style="width: 60%">
                    <el-input v-model="editForm.coursetime"></el-input>
                </el-form-item>
                <el-form-item label="" prop="classroom" style="width: 60%">
                    <el-input v-model="editForm.classroom"></el-input>
                </el-form-item>
                <el-form-item label="" prop="courseweek" style="width: 40%">
                    <el-input v-model="editForm.courseweek" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="coursetype" style="width: 40%">
                    <el-input v-model="editForm.coursetype" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="classname" style="width: 60%">
                    <el-input v-model="editForm.classname" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="credit" style="width: 40%">
                    <el-input v-model="editForm.credit" :disabled="true"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisble = false"> </el-button>
                <el-button type="primary" @click="editCourseInfo"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { courseAddFormRulesMixin } from '@/common/mixin.js'
import Breadcrumb from '../content/breadcrumb/Breadcrumb.vue'
export default {
  mixins: [courseAddFormRulesMixin],
  data () {
    return {
      // 
      currentPage: 1,
      // 
      pageSize: 5,
      // 
      formCourse: { coursename: '', name: '', classname: '' },
      ClassName: [{ name: '', id: '' }],
      CourseType: [{ label: '', value: '' }, { label: '', value: '' }],
      courseData: {
        courseList: [],
        total: 0
      },
      editDialogVisble: false,
      userInfo: {},
      editForm: {}
    }
  },
  methods: {
    async getClassName () {
      const { data: res } = await this.$http.get('util/class')
      this.ClassName = res.data
      // console.log(this.ClassName)
      // console.log(this.DepartmentName)
    },
    async getCourseList () {
      const { data: res } = await this.$http.get('admin/courseTable/' + this.currentPage + '/' + this.pageSize, {
        params: this.formCourse
      })
      if (res.code === 20041) {
        // this.$message.success(res.msg)
        this.courseData.courseList = res.data.records
        this.courseData.total = res.data.total
      } else {
        this.$message.error(res.msg)
      }
      this.formCourse = { coursename: '', name: '', classname: '' }
      // console.log(res)
    },
    //  pagesize  
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.pageSize = newSize
      this.getCourseList()
    },
    //    
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.currentPage = newPage
      this.getCourseList()
    },
    async showEditDialog (courseid) {
      const { data: res } = await this.$http.get('admin/courseTable/' + courseid)
      if (res.code !== 20041) {
        return this.$message.error('~')
      }
      this.editForm = res.data
      this.editDialogVisble = true
      // return this.$message.success('~')
    },
    // 
    aditClosed () {
      this.$refs.editFormRef.resetFields()
    },
    editCourseInfo () {
      this.$refs.editFormRef.validate(async valid => {
        console.log(valid)
        if (!valid) return
        // 
        const { data: res } = await this.$http.put('admin/courseTable', this.editForm)
        if (res.code !== 20031) {
          return this.$message.error('!')
        }else{
          this.$message.success('!')
        }
        this.editDialogVisble = false
        this.getCourseList()
      })
    },
    // id
    async removeCourseById (courseid) {
      // 
      const confirmRusult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      console.log(confirmRusult)
      // , confirm
      // , cancel
      if (confirmRusult !== 'confirm') {
        return this.$message.info('')
      }
      this.$http.delete('admin/courseTable/' + courseid).then(res => {
        const { data: response } = res
        console.log(response)
        if (response.code !== 20021) {
          return this.$message.error('')
        }else{
          this.$message.success('')
        }
        this.getCourseList()
      })
    }
  },
  created () {
    this.getClassName()
    this.getCourseList()
  },
  components: {
    Breadcrumb
  }
}
</script>

<style lang="less" scoped>
</style>
