<template>
    <div class="score">
        <Breadcrumb name1=""/>
        <!--  -->
        <el-card class="box-card">
            <el-table :data="scoreData.scoreList" stripe style="widt: 100%" border>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="courseName" label=""></el-table-column>
                <el-table-column prop="teacherid" label=""></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="score" label=""></el-table-column>
                <el-table-column prop="credit" label=""></el-table-column>
            </el-table>
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                @size-change="handleSizeChange"
                :page-size="pageSize"
                :page-sizes="[5, 9, 13, 15]"
                layout="total, sizes, prev, pager, next, jumper"
                :total="scoreData.total"
            >
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
import Breadcrumb from '../content/breadcrumb/Breadcrumb.vue'
export default {
  data () {
    return {
      // 
      currentPage: 1,
      // 
      pageSize: 5,
      scoreData: {
        scoreList: [],
        total: 0
      }
    }
  },
  methods: {
    async getScoreList () {
      const { data: res } = await this.$http.get('student/score/' + this.currentPage + '/' + this.pageSize)
      if (res.code === 20041) {
        // this.$message.success(res.msg)
        this.scoreData.scoreList = res.data.records
        this.scoreData.total = res.data.total
      } else {
        this.$message.error(res.msg)
      }
      // console.log(res)
    },
    //  pagesize  
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.pageSize = newSize
      this.getScoreList()
    },
    //    
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.currentPage = newPage
      this.getScoreList()
    }
  },
  created () {
    this.getScoreList()
  },
  components: {
    Breadcrumb
  }
}
</script>

