<template>
    <div class="courseTable">
        <Breadcrumb name1=""/>
        <!--  -->
        <el-card class="box-card">
            <el-table :data="courseData.courseList" stripe style="widt: 100%" border>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="coursename" label=""></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="coursetime" label=""></el-table-column>
                <el-table-column prop="classroom" label=""></el-table-column>
                <el-table-column prop="courseweek" label=""></el-table-column>
                <el-table-column prop="coursetype" label=""></el-table-column>
                <el-table-column prop="classname" label=""></el-table-column>
                <el-table-column prop="credit" label=""></el-table-column>
            </el-table>
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                @size-change="handleSizeChange"
                :page-size="pageSize"
                :page-sizes="[5, 9, 13, 15]"
                layout="total, sizes, prev, pager, next, jumper"
                :total="courseData.total"
            >
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
import Breadcrumb from '../content/breadcrumb/Breadcrumb.vue'
export default {
  data () {
    return {
      // 
      currentPage: 1,
      // 
      pageSize: 5,
      courseData: {
        courseList: [],
        total: 0
      }
    }
  },
  methods: {
    async getCourseList () {
      const { data: res } = await this.$http.get('student/courseTable/' + this.currentPage + '/' + this.pageSize, {
        params: this.formCourse
      })
      if (res.code === 20041) {
        // this.$message.success(res.msg)
        this.courseData.courseList = res.data.records
        this.courseData.total = res.data.total
      } else {
        this.$message.error(res.msg)
      }
      this.formCourse = { coursename: '', classname: '' }
      // console.log(res)
    },
    //  pagesize  
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.pageSize = newSize
      this.getCourseList()
    },
    //    
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.currentPage = newPage
      this.getCourseList()
    }
  },
  created () {
    this.getCourseList()
  },
  components: {
    Breadcrumb
  }
}
</script>

<style lang="less" scoped>
</style>
