<template>
    <div class="graduate">
        <Breadcrumb name1=""/>
        <!--  -->
        <el-card class="box-card">
            <el-row :gutter="20">
                <el-col :span="20">
                    <el-form :inline="true" :model="formGraduate">
                        <el-form-item label=":">
                            <el-input v-model="formGraduate.name" placeholder="" style="width: 140px"></el-input>
                        </el-form-item>
                        <el-form-item label=":" prop="classname">
                            <el-select v-model="formGraduate.classname" placeholder="" style="width: 160px">
                                <el-option
                                v-for="item in ClassName"
                                :key="item.id"
                                :label="item.name"
                                :value="item.name">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="getGraduateList"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
            <el-table :data="graduateData.graduateList" stripe style="widt: 100%" border>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column prop="studentid" label=""></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="classname" label=""></el-table-column>
                <el-table-column prop="credit1" label=""></el-table-column>
                <el-table-column prop="credit2" label=""></el-table-column>
                <el-table-column prop="status" label=""></el-table-column>
                <el-table-column label="" width="180px">
                <template v-slot="scope">
                    <!--  -->
                    <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.studentid)"></el-button>
                </template>
                </el-table-column>
            </el-table>
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                @size-change="handleSizeChange"
                :page-size="pageSize"
                :page-sizes="[5, 9, 13, 15]"
                layout="total, sizes, prev, pager, next, jumper"
                :total="graduateData.total"
            >
            </el-pagination>
        </el-card>
        <el-dialog title="" @close="aditClosed" :visible.sync="editDialogVisble" width="35%">
            <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="90px">
                <el-form-item label="" prop="studentid" style="width: 60%">
                    <el-input v-model="editForm.studentid" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name" style="width: 60%">
                    <el-input v-model="editForm.name" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="classname" style="width: 60%">
                    <el-input v-model="editForm.classname" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="credit1" style="width: 60%">
                    <el-input v-model="editForm.credit1" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="credit2" style="width: 60%">
                    <el-input v-model="editForm.credit2" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="status">
                            <el-select v-model="editForm.status" placeholder="" style="width: 160px">
                                <el-option
                                v-for="item in Status"
                                :key="item.value"
                                :label="item.label"
                                :value="item.label">
                                </el-option>
                            </el-select>
                        </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisble = false"> </el-button>
                <el-button type="primary" @click="editscoreInfo"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import Breadcrumb from '../content/breadcrumb/Breadcrumb.vue'
export default {
  data () {
    return {
      // 
      currentPage: 1,
      // 
      pageSize: 5,
      // 
      formGraduate: {},
      ClassName: [{ name: '', id: '' }],
      Status: [{ label: '', value: '1' }, { label: '', value: '2' }],
      graduateData: {
        graduateList: [],
        total: 0
      },
      editDialogVisble: false,
      editForm: {}
    }
  },
  methods: {
    async getClassName () {
      const { data: res } = await this.$http.get('util/class')
      this.ClassName = res.data
      // console.log(this.ClassName)
      // console.log(this.DepartmentName)
    },
    async getGraduateList () {
      const { data: res } = await this.$http.get('admin/graduate/' + this.currentPage + '/' + this.pageSize, {
        params: this.formGraduate
      })
      if (res.code === 20041) {
        // this.$message.success(res.msg)
        this.graduateData.graduateList = res.data.records
        this.graduateData.total = res.data.total
      } else {
        this.$message.error(res.msg)
      }
      this.formGraduate = { name: '', classname: '' }
      // console.log(res)
    },
    //  pagesize  
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.pageSize = newSize
      this.getGraduateList()
    },
    //    
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.currentPage = newPage
      this.getGraduateList()
    },
    async showEditDialog (studentid) {
      const { data: res } = await this.$http.get('admin/graduate/' + studentid)
      if (res.code !== 20041) {
        return this.$message.error('~')
      }
      this.editForm = res.data
      this.editDialogVisble = true
      // return this.$message.success('~')
    },
    // 
    aditClosed () {
      this.$refs.editFormRef.resetFields()
    },
    editscoreInfo () {
      this.$refs.editFormRef.validate(async valid => {
        console.log(valid)
        if (!valid) return
        // 
        const { data: res } = await this.$http.put('admin/graduate', this.editForm)
        if (res.code !== 20031) {
          return this.$message.error('!')
        }else{
          this.$message.success('!')
        }
        this.editDialogVisble = false
        this.getGraduateList()
      })
    }
  },
  created () {
    this.getClassName()
    this.getGraduateList()
  },
  components: {
    Breadcrumb
  }
}
</script>

<style lang="less" scoped>
</style>
