export const userAddFormRulesMixin = {
  data () {
    // 
    const checkEmail = (rule, value, callback) => {
      const regEmail = /^\w+@\w+(\.\w+)+$/ // 
      if (regEmail.test(value)) {
        return callback() // 
      }
      // 
      callback(new Error(''))
    }
    // 
    const checkMobeli = (rule, value, callback) => {
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        return callback()
      }
      // 
      callback(new Error(''))
    }

    // 
    const checkUserid = (rule, value, callback) => {
      const router_path = this.$route.path
      let regUserid = ''
      if (router_path === '/student') {
        // console.log(router_path)
        regUserid = /^12\d{3,4}$/
        if (regUserid.test(value)) {
          return callback()
        }
        // 
        callback(new Error('125-6'))
      }
      if (router_path === '/teacher') {
        // console.log(router_path)
        regUserid = /^11\d{3,4}$/ // 
        if (regUserid.test(value)) {
          return callback() // 
        }
        // 
        callback(new Error('115-6'))
      }
    }

    const checkDate = (rule, value, callback) => {
      const date = new Date()
      // console.log(date)
      // console.log(new Date(value))
      if (new Date(value) <= date) {
        return callback()
      }
      // 
      callback(new Error(''))
    }
    return {
      // 
      addFormRules: {
        userid: [
          { required: true, message: '/', trigger: 'blur' },
          { validator: checkUserid, trigger: 'blur' },
          { type: 'number', message: '/' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        departmentName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        className: [
          { required: true, message: '', trigger: 'blur' }
        ],
        tel: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobeli, trigger: 'blur' }
        ],
        birthday: [
          { validator: checkDate, trigger: 'blur' }
        ],
        indate: [
          { validator: checkDate, trigger: 'blur' }
        ]
      }
    }
  }
}

export const updatePwdFormRulesMixin = {
  data () {
    const checkNewPwd = (rule, value, callback) => {
      if (value === this.updatePwdForm.newPwd) {
        return callback() // 
      }
      // 
      callback(new Error(''))
    }
    return {
      // 
      updatePwdFormRules: {
        oldPwd: [
          { required: true, message: '', trigger: 'blur' }
        ],
        newPwd: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ],
        newPwd2: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkNewPwd, trigger: 'blur' }
        ]
      }
    }
  }
}

export const courseAddFormRulesMixin = {
  data () {
    const checkCredit = (rule, value, callback) => {
      const regCredit = /(^[0-9]{1,2}$)|(^[0-9]{1,2}[\.]{1}[0-9]{1,2}$)/
      if (regCredit.test(value)) {
        return callback()
      }
      // 
      callback(new Error('1-2'))
    }
    return {
      // 
      addFormRules: {
        coursename: [{ required: true, message: '', trigger: 'blur' }],
        teacherid: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', message: '' }
        ],
        classname: [{ required: true, message: '', trigger: 'blur' }],
        classid: [{ required: true, message: '', trigger: 'blur' }],
        credit: [{ required: true, message: '', trigger: 'blur' }, { validator: checkCredit, trigger: 'blur' }]
      }
    }
  }
}

