export const employmentFormRulesMixin = {
  data () {
    const checkWages = (rule, value, callback) => {
      const regWages = /(^[0-9]{4,6}$)|(^[0-9]{4,6}[\.]{1}[0-9]{1,2}$)|^$/
      if (regWages.test(value)) {
        return callback()
      }
      // 
      callback(new Error('4-6'))
    }
    return {
      // 
      employmentFormRules: {
        wages: [{ validator: checkWages, trigger: 'blur' }]
      }
    }
  }
}
