<template>
    <div class="users">
    <Breadcrumb name1="" name2="" />
    <!--  -->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="16">
        <!--  -->
            <el-form :inline="true" :model="formStu">
                <el-form-item label=":">
                    <el-input v-model="formStu.name" placeholder="" style="width: 160px"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="gender">
                    <el-select v-model="formStu.gender" placeholder="" style="width: 160px">
                        <el-option
                        v-for="item in Gender"
                        :key="item.value"
                        :label="item.label"
                        :value="item.label">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label=":" prop="className">
                    <el-select v-model="formStu.className" placeholder="" style="width: 160px">
                        <el-option
                        v-for="item in ClassName"
                        :key="item.id"
                        :label="item.name"
                        :value="item.name">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="getUserList"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <el-col :span="4">
          <!--  -->
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userData.userList" stripe style="widt: 100%" border>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="userid" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="gender" label=""></el-table-column>
        <el-table-column prop="birthday" label=""></el-table-column>
        <el-table-column prop="indate" label=""></el-table-column>
        <el-table-column prop="tel" label=""></el-table-column>
        <el-table-column prop="className" label=""></el-table-column>
        <el-table-column label="" width="180px">
          <template v-slot="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.userid)"></el-button>
            <!--  -->
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.userid)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        @size-change="handleSizeChange"
        :page-size="pageSize"
        :page-sizes="[5, 9, 13, 15]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="userData.total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="35%" @close="addDislogClosed">
      <!--  -->
      <el-form label-width="70px" ref="addFormRef" :model="addForm" :rules="addFormRules">
        <el-form-item label="" prop="userid" style="width: 70%">
          <el-input v-model.number="addForm.userid"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name" style="width: 40%">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="gender">
            <el-select v-model="addForm.gender" placeholder="">
                <el-option
                v-for="item in Gender"
                :key="item.value"
                :label="item.label"
                :value="item.label">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="birthday">
            <el-date-picker
                v-model="addForm.birthday"
                type="date"
                placeholder="">
            </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="indate">
            <el-date-picker
                v-model="addForm.indate"
                type="date"
                placeholder="">
            </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="tel" style="width: 70%">
          <el-input v-model.number="addForm.tel"></el-input>
        </el-form-item>
        <el-form-item label="" prop="className">
            <el-select v-model="addForm.className" placeholder="">
                <el-option
                v-for="item in ClassName"
                :key="item.id"
                :label="item.name"
                :value="item.name">
                </el-option>
            </el-select>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" @close="aditClosed" :visible.sync="editDialogVisble" width="35%">
      <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="" prop="userid" style="width: 70%">
          <el-input v-model.number="editForm.userid" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name" style="width: 40%">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="gender">
            <el-select v-model="editForm.gender" placeholder="">
                <el-option
                v-for="item in Gender"
                :key="item.value"
                :label="item.label"
                :value="item.label">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="birthday">
            <el-date-picker
                v-model="editForm.birthday"
                type="date"
                placeholder="">
            </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="indate">
            <el-date-picker
                v-model="editForm.indate"
                type="date"
                placeholder="">
            </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="tel" style="width: 70%">
          <el-input v-model.number="editForm.tel"></el-input>
        </el-form-item>
        <el-form-item label="" prop="className">
            <el-select v-model="editForm.className" placeholder="">
                <el-option
                v-for="item in ClassName"
                :key="item.id"
                :label="item.name"
                :value="item.name">
                </el-option>
            </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisble = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { userAddFormRulesMixin } from '@/common/mixin.js'
import Breadcrumb from '../content/breadcrumb/Breadcrumb.vue'
export default {
  mixins: [userAddFormRulesMixin],
  data () {
    return {
      // 
      currentPage: 1,
      // 
      pageSize: 5,
      // 
      formStu: {
        // 
        name: '',
        gender: '',
        className: ''
      },
      Gender: [{ label: '', value: 'nan' }, { label: '', value: 'nv' }],
      ClassName: [{ name: '', id: '' }],
      Role: '',
      // 
      userData: {
        userList: [],
        total: 0
      },
      // 
      addDialogVisible: false,
      // 
      addForm: {},
      // 
      editDialogVisble: false,
      // 
      userInfo: {},
      // 
      editForm: {}
    }
  },
  components: {
    Breadcrumb
  },
  created () {
    this.getRole()
    this.getUserList()
    this.getClassName()
  },
  methods: {
    getRole () {
      const role = window.sessionStorage.getItem('role')
      if (role === '1') {
        this.Role = 'admin'
        // console.log(this.Role)
      }
      if (role === '2') {
        this.Role = 'teacher'
      }
    },
    async getClassName () {
      const { data: res } = await this.$http.get('util/class')
      this.ClassName = res.data
      console.log(this.ClassName)
      // console.log(this.DepartmentName)
    },
    async getUserList () {
      const { data: res } = await this.$http.get(this.Role + '/info/student/' + this.currentPage + '/' + this.pageSize, {
        params: this.formStu
      })
      if (res.code === 20041) {
        // this.$message.success(res.msg)
        this.userData.userList = res.data.records
        this.userData.total = res.data.total
      } else {
        this.$message.error(res.msg)
      }
      this.formStu = { name: '', gender: '', className: '' }
      // console.log(res)
    },
    //  pagesize  
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.pageSize = newSize
      this.getUserList()
    },
    //    
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.currentPage = newPage
      this.getUserList()
    },
    // 
    addDislogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // ,
    addUser () {
      this.$refs.addFormRef.validate(async valid => {
        console.log(valid)
        if (!valid) return
        // 
        const { data: res } = await this.$http.post(this.Role + '/info/student', this.addForm)
        if (res.code !== 20011) {
          return this.$message.error('')
        }else{
          this.$message.success('')
        }
        // 
        this.addDialogVisible = false
        // ,
        this.getUserList()
      })
    },
    // 
    async showEditDialog (userid) {
      const { data: res } = await this.$http.get(this.Role + '/info/student/' + userid)
      if (res.code !== 20041) {
        return this.$message.error('~')
      }
      this.editForm = res.data
      console.log(this.editForm)
      this.editDialogVisble = true
      // return this.$message.success('~')
    },
    // 
    aditClosed () {
      this.$refs.editFormRef.resetFields()
    },
    editUserInfo () {
      this.$refs.editFormRef.validate(async valid => {
        console.log(valid)
        if (!valid) return
        // 
        const { data: res } = await this.$http.put(this.Role + '/info/student', this.editForm)
        if (res.code !== 20031) {
          return this.$message.error('!')
        }else{
          this.$message.success('!')
        }
        this.editDialogVisble = false
        this.getUserList()
      })
    },
    // id
    async removeUserById (userid) {
      // 
      const confirmRusult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      console.log(confirmRusult)
      // , confirm
      // , cancel
      if (confirmRusult !== 'confirm') {
        return this.$message.info('')
      }
      this.$http.delete(this.Role + '/info/student/' + userid).then(res => {
        const { data: response } = res
        console.log(response)
        if (response.code !== 20021) {
          return this.$message.error('')
        }else{
          this.$message.success('')
        }
        this.getUserList()
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 15px;
}
</style>
